<template>
 <div class="history">
  <h3>H I S T O R Y</h3>
  <div class="container">
    <div class="flex">
      
    <p
    v-for="(item,index) in history" 
    :key="index"
    :class="activeIndex(value).includes(index) && 'bold'"
    >
      {{ item }}
    </p>
    </div>
    <center>
      <!-- {{ activeIndex(value) }} -->
      <input type="number" class="input" v-model="value">
    </center>
  </div>
 </div>
</template>

<script>
import {mapState,mapGetters} from "vuex"
export default {
  name: 'History',
  data(){
    return{
      value:0,
      active:0
    }
  },
  computed:{
    ...mapState(["history"]),
    ...mapGetters(["activeIndex"])
  }
}
</script>
