<template>
 <div>
   <h1 class="title">M <span class="blue">O</span> N <span class="blue">O</span> </h1>
   <div class="box">
    <h2 class="counter"> {{ counter }} </h2>
   </div>
   <div class="grp">
     <button class="button button-primary" @click="subCounter(value)">-</button>
     <input type="number" v-model="value">
     <button class="button button-primary" @click="addCounter(value)">+</button>
   </div>
   <button class="button" @click="addRandomNumber">add Randomly </button>
 </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  name: 'Counter',
  data(){
    return{
      value:0
    }
  },
  computed:{
    ...mapState(["counter"])
  },
  methods:{
    ...mapMutations(["addCounter","subCounter"]),
    ...mapActions(["addRandomNumber"])
  }
}
</script>
