/*
axios
- import 
- under action async function
- get url data
*/
// to create store obj
import { createStore } from 'vuex'
// to access axios func()
import axios from 'axios'

export default createStore({
  // storage/data
  state: {
    counter:0,
    history:[0]
  },
  // to change state
  mutations: {
    addCounter(state,val){
      state.counter += val;
      state.history.push(state.counter)

    },
    subCounter(state,val){
      state.counter -= val;
      state.history.push(state.counter)
    }
  },
  // to fetch data / async operation
  actions: {
    async addRandomNumber(context){
      let data = await axios.get('https://random-data-api.com/api/number/random_number');
      let val = Math.trunc(data.data.decimal);
      context.commit("addCounter",val)
      console.log(val)
    }
  },
  getters:{
    //- get index of value entered through input
    activeIndex:(state)=>(value)=>{
      // collect the eindex
      let indexes=[];
      state.history.forEach((item,index)=>{
        if(item == value){
          indexes.push(index)
        }
      })
        return indexes;
      
    }
  },
  modules: {
  }
})
